.customNode {
  font-size: 1.2em;

  span {
    line-height: 10px;
    margin-bottom: 12px;
  }
}

.position {
  white-space: nowrap;
  overflow: visible;

  .typed {
    font-style: italic;
    color: #37ffe1;
  }
}

.workFlowNode {
  padding: 50px 0;

  &:hover {
    Image {
      animation: wobble 1s ease-in-out;
    }
  }
}
