.profile {
  display: flex;
  align-items: center;
  justify-content: center;

  .details {
    text-align: left;

    p {
      margin-bottom: 16px;
    }
  }

  .avatar {
    padding: 50px 0;

    img {
      border-radius: 50%;
      max-width: 100%;
    }
  }

  .lottie {
    height: 200px;
  }

  .name {
    p {
      margin-bottom: 48px;
      font-size: 1em;
      color: black;
      text-align: start;
      font-family: HongLei, SiYuanHeiTi, Helvetica, Arial, sans-serif;
    }
    h1 {
      margin-bottom: 48px;
      font-size: 2em;
      color: black;
      text-align: start;
      text-shadow: none;
      font-weight: bold;
      font-family: HongLei, SiYuanHeiTi, Helvetica, Arial, sans-serif;
    }
  }

  @media screen and (max-width: 992px) {
    .name {
      display: flex;
      justify-content: center;
    }
  }
}
